body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

 a{
  margin: .5em;
  text-decoration: none;
  color: black;
}

#nav{
  padding: 0 5em;
}
.home{
  display: grid;
  grid-template-areas: "a"
  "b";
}

.top{
  grid-area: "a";
  display: -webkit-flex;
  display: flex;
  background-image: url("https://i.imgur.com/PepWXZB.jpg");
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 945px;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}

.mid{
  grid-area: "b";
  background-color: whitesmoke;
  width: 100%;
  height: 800px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.info{
  margin: 0;
  text-align: center;
  font-size: 2em;
  padding: 2em;
}

.info i{
  font-size: 3em;
}

.big{
  font-size: 4em;
}

.sub{
  font-size: 1.5em;
}

#bigger-button{
  padding: .5em 1.5em;
  font-size: 1.5em;
}

#example{
  border-radius: 50%;
  min-height: 500px;
  max-width: 500px;
}

footer{
  text-align: center;
  font-size: 1em;
  box-shadow: 2px 2px 2px 2px lightgray;
}

#down{
  margin-top: 3em;
  color: white;
  font-size: 3em;
}

#down a {
  color: white;
}
.prof {
  max-width: 382px;
  max-width: 400px;
}

.dashboard{
  display: grid;
  grid-template-areas: 
  "search"
  "main";
  margin-top: 5em;
  text-align: center;
}

.search{
  grid-area: "search";
  box-shadow: 2px 2px whitesmoke;
  border: solid 1px lightgrey;
  font-family: 'Heebo', sans-serif;
  width: 40%;
  height: 2em;
  margin: auto;
  padding: .25em;
}

.main{
  grid-area: 'main';
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 85%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Heebo', sans-serif;
  margin: auto;
}
.App{
  font-family: 'Roboto';
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

a{
  text-decoration: none;
}
